<template>
<div 
  id="about"
  style="
  position:absolute;
  x:0;
  y:0;
  width:100%;
  display: inline-block;">
  <div 
    id="videoContainerDiv"
    style="
      position:absolute;
      width:50vw;
      x:0px;
      y:0px;
      display: inline;">
      <vimeo-player 
           ref="player" 
          :video-id="videoID" 
          @ready="onReady" 
          :player-height="height"
          :player-width="width">
      </vimeo-player>    
    </div>
  <div 
    style="
      position:absolute;
      width:40vw;
      padding-left:50vw;
      y:0px;
      display: inline;">      
      <h1
        style="
          position:absolute;
          width:20vw;
          margin: 0;
          padding-top:0;
          white-space: nowrap;
          ">
          About Justin
      </h1>
      <p style="
          position:absolute;
          width:24vw;
          padding-top:36px;">
      From a young age, I was fascinated by film.
      The Visuals of movies like Lord of the Rings
      and Jurassic park stuck with me while I was growing
      up in Eighty Four, PA. I started to realize
      how much cinematography can change the mood of the story.
      While in school I was constantly thinking about making films.
      I later made the decision to pursue filmmaking which lead me
      to go to Point Park University where I got my bachelors degree
      in Cinema Productions with a concentration in cinematography.
      Since then I’ve worked on a number of films and with various
      artists such as Grammy award-winning artist Daya.
      I love bringing great stories to life and working with
      directors to make strong visuals through lighting and camera
      work.
      </p>
      <img
        src="../assets/bio/headshot.png"
        style="
          position:absolute;
          padding-left:24vw;
          width:20vw;">
    </div>
</div>
</template>

<script>

import Vue from 'vue'
import vueVimeoPlayer from 'vue-vimeo-player'

Vue.use(vueVimeoPlayer)



export default {
    name: 'about',
    data() {
		  return {
			  videoID: '263113001',
        height: window.innerWidth/4,
        width: window.innerWidth/2,
			  options: {},
			  playerReady: false
		  }
    },
	  methods: {
		  onReady() {
			  this.playerReady = true
		  },
		  play () {
			  this.$refs.player.play()
		  },
		  pause () {
			  this.$refs.player.pause()
      }
	  },
    components: { vueVimeoPlayer }
}
</script>



