<template>
  <div id="home">
    <section class="SlideShow">
      <ImageSlider :slides="imageSlides"/>
    </section>
  </div>
</template>

<script>
import Vue from 'vue';
import ImageSlider from './ImageSlider';
import SliderFrame from './SliderFrame';
import SliderSlide from './SliderSlide.vue';
import SliderSlides from './SliderSlides.vue';

import slide1 from '../assets/slideshow/1.jpg';
import slide2 from '../assets/slideshow/2.jpg';
import slide3 from '../assets/slideshow/3.jpg';
import slide4 from '../assets/slideshow/4.jpg';

export default {
  components: {
    ImageSlider,
    SliderFrame,
    SliderSlide,
    SliderSlides,
  },
  name: "home",
  data() {
    return {
      imageSlides: [
        {
          image: slide1,
        },
        {
          image: slide2,
        },
        {
          image: slide3,
        },
        {
          image: slide4,
        },
      ],
    };
  },  
};
</script>

<style>
.SlideShow {
  padding-left: 12vw;
  width: 50vw;
  height: 25vw;
}
.SliderSlide--transition-left-enter-active, .SliderSlide--transition-left-leave-active, .SliderSlide--transition-right-enter-active, .SliderSlide--transition-right-leave-active {
  transition-duration: 0.5s;
  transition-property: height, opacity, transform;
  transition-timing-function: cubic-bezier(0.55, 0, 0.1, 1);
  overflow: hidden;
}
.SliderSlide--transition-left-leave-active, .SliderSlide--transition-right-leave-active {
  top: 0;
  position: absolute;
}
.SliderSlide--transition-left-enter, .SliderSlide--transition-right-leave-active {
  opacity: 0;
  transform: translate(2em, 0);
}
.SliderSlide--transition-left-leave-active, .SliderSlide--transition-right-enter {
  opacity: 0;
  transform: translate(-2em, 0);
}
.SliderSlides {
  position: relative;
}
.ImageSlider {
  position: relative;
}
.ImageSlider__headline, .ImageSlider__direction {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}
.ImageSlider__direction--prev {
  background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' fill='%23fff' viewBox='0 0 8 8'%3e%3cpath d='M5.25 0l-4 4 4 4 1.5-1.5-2.5-2.5 2.5-2.5-1.5-1.5z'/%3e%3c/svg%3e");
  display: inline-block;
  width: 10%;
  height: 100%;
  background-size: 100% 100%;
  opacity: 0.5;
}
.ImageSlider__direction--next {
  background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' fill='%23fff' viewBox='0 0 8 8'%3e%3cpath d='M2.75 0l-1.5 1.5 2.5 2.5-2.5 2.5 1.5 1.5 4-4-4-4z'/%3e%3c/svg%3e");
  display: inline-block;
  width: 10%;
  height: 100%;
  left: 90%;
  background-size: 100% 100%;
  opacity: 0.5;
}
.ImageSlider__slide, .ImageSlider__image {
  width: 100%;
}
.ImageSlider__slide::before {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.2);
  content: '';
}
.ImageSlider__headline {
  width: 100%;
  text-align: center;
  font-size: 2.5em;
  color: #fff;
  text-shadow: 0 0 0.75em rgba(0, 0, 0, 0.4);
}
.ImageSlider__image {
  vertical-align: middle;
}
.ImageSlider__dots {
  position: absolute;
  right: 0;
  bottom: 0.5em;
  left: 0;
  display: flex;
  justify-content: center;
  list-style: none;
  position: absolute;
  padding-left: 0;
  margin-right: 15%;
  margin-left: 15%;
}
</style>





