<template>
  <SliderFrame>
    <div
      slot-scope="{ goToIndex, next, prev }"
      class="ImageSlider"
    >
      <SliderSlides>
        <SliderSlide
          v-for="slide in slides"
          :key="slide.image"
          class="ImageSlider__slide"
        >
          <img
            :src="slide.image"
            :alt="slide.headline"
            class="ImageSlider__image"
          >
          <h2 class="ImageSlider__headline">
            {{ slide.headline }}
          </h2>
        </SliderSlide>
      </SliderSlides>
      <div class="carousel-control-prev">
      <button
        class="ImageSlider__direction ImageSlider__direction--prev"
        @click="prev"
      >
      </button>      
      </div>
      <div class="carousel-control-next">
      <button
        class="ImageSlider__direction ImageSlider__direction--next"
        @click="next"
      >
        
      </button>
      </div>
      <ol class="ImageSlider__dots">
        <li
          v-for="n in slides.length"
          :key="n"
          class=""
        >
          <button @click="goToIndex(n - 1)">
            {{ n }}
          </button>
        </li>
      </ol>
    </div>
  </SliderFrame>
</template>

<script>
import SliderFrame from './SliderFrame';
import SliderSlide from './SliderSlide.vue';
import SliderSlides from './SliderSlides.vue';

export default {
  name: `ImageSlider`,
  components: {
    SliderFrame,
    SliderSlide,
    SliderSlides,
  },
  props: {
    slides: {
      default: () => [],
      type: Array,
    },
  },
};
</script>



