<template>
  <div id="commercial">
    <transition-group name="thumbnailfade" tag="div">
    <img v-for="thumb in images" 
         :key="thumb.id" 
         @click="showLightbox(thumb.name)"  
         :src="require('../assets/commercial/' + thumb.name)" 
         :alt="thumb.alt" 
         :title="thumb.alt" />
  </transition-group>
  
  <lightbox id="lightbox" 
      ref="lightbox"
      :images="images"
      :directory="thumbnailDir"
      :timeoutDuration="5000"
  />
  </div>
</template>

<script>
import Vue from 'vue';
import Lightbox from './lightbox.vue';

Vue.use(Lightbox);
Vue.component('lightbox', Lightbox);

var photoDir = 
  'commercial';

var imageList = 
  [{'name':'Coffee shop video.jpg', 
    'alt':'The Dolomites', 
    'id':'image1' },   
   {'name':'Daya Jingle Ball.jpg', 
    'alt':'It is a bird on a tree!', 
    'id':'image2'  }, 
   
   {'name':'farm video.jpg', 
    'alt':'I will live here someday', 
    'id':'image3'  },
    {'name':'scan SD Headshot - 1.1.png', 
    'alt':'The Dolomites', 
    'id':'image4' },
   
 ];
    
export default {
  name: 'commercial',
  data() {
    return {
      thumbnailDir: photoDir,
      images: imageList
    }
  },
  methods: {
    showLightbox: function(imageName) {
      this.$refs.lightbox.show('../assets/commercial/' + imageName);
    },
    
  }
  
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
img {
  width: 270px;
  height: 180px;
  margin: 20px;
  border-radius: 3px;
  cursor: pointer;
  transition: all 0.4s ease;
}

.thumbnailfade-leave-active,
.thumbnailfade-enter-active {
  transition: all 0.4s ease;
}

.thumbnailfade-enter-active {
  transition-delay: 0.4s;
}

.thumbnailfade-enter,
.thumbnailfade-leave-to {
  opacity: 0;
}
</style>



